import React, { Component } from 'react';
import { motion } from 'framer-motion';
// import { Route, Switch } from 'react-router-dom'; import LikeDislike from '../LikeDislike/LikeDislike';
import ReactTooltip from 'react-tooltip';
import ModalPerson from '../Modals/ModalPerson';
import ModalOther from '../Modals/ModalOther';
import imgLisa from '../../media/img_lisa_circle.png';
import imgDanielle from '../../media/img_danielle_circle.png';
import imgBrian from '../../media/img_brian_circle.png';
import '../../styles/main.scss';


function BottomSection () {
  return (
    <div>
      <div className='wrapper-down'>
        <div>
          <h4 className='font-header'><span data-tip=' ' data-for={'dataTipTeam'}>Team</span></h4>
          <ReactTooltip id={'dataTipTeam'} className='tooltip' place='top'>
            <span> Meet members of our crew who plays significant role in the team </span>
          </ReactTooltip>
          <ModalPerson name='Lisa' header='Meet the team' img={imgLisa} />
          <ModalPerson name='Danielle' header='Meet the team' img={imgDanielle} />
          <ModalPerson name='Brian' header='Meet the team' img={imgBrian} />
          <p className='thick-join-us'>Join us!</p>
        </div>
        <div>
          <h4 className='font-header'><span data-tip=' ' data-for={'dataTipLearnMore'}>Learn more</span></h4>
          <ReactTooltip id={'dataTipLearnMore'} className='tooltip' place='top'>
            <span> Learn more about our work methods, sample of works or client user stories </span>
          </ReactTooltip>
          {/* <p className='thin'>Manifesto</p> */}
          <ModalOther name='Manifesto' header='Manifesto' />
          <ModalOther name='Works' header='Works' />
          <ModalOther name='Stories' header='Stories' />
        </div>
        <div>
          <h4 className='font-header'><span data-tip=' ' data-for={'dataTipMeetUs'}>Meet us</span></h4>
          <ReactTooltip id={'dataTipMeetUs'} className='tooltip' place='top'>
            <span> Get to know our studio, user community or workshops we organize </span> 
          </ReactTooltip>
          {/* <p className='thin'>Studio</p> */}
          <ModalOther name='Studio' header='Studio' />
          <ModalOther name='Community' header='Community' />
          <ModalOther name='Workshops' header='Workshops' />
        </div>
        <div>
          <h4 className='font-header'><span data-tip=' ' data-for={'dataTipContact'}>Contact</span></h4>
          <ReactTooltip id={'dataTipContact'} className='tooltip' place='top'>
            <span> Contact us by visiting our official channels on social web portals </span>
          </ReactTooltip>
          <p className='thin'>Twitter</p>
          <p className='thin'>Facebook</p>
          <p className='thin'>Instagram</p>
        </div>
      </div>
    </div>
  );
}

export default BottomSection;
